<template>
  <div class="user-profile">
    <!-- 导航栏 -->
    <van-nav-bar
      title="个人信息"
      left-arrow
      @click-left="$router.back()"
      fixed
    />

    <div class="main-wrap">
      <!-- 个人信息区域 -->
      <van-cell title="头像" is-link @click="$refs['file'].click()">
        <template #default>
          <van-image
            class="avatar"
            round
            fit="cover"
            :src="user.photo"
          />
        </template>
      </van-cell>
      <van-cell title="昵称" is-link :value="user.name" @click="isUpdateNameShow = true"/>
      <van-cell title="性别" is-link :value="user.gender === 0 ? '男' : '女'"  @click="isUpdateGenderShow = true"/>
      <van-cell title="生日" is-link :value="user.birthday" @click="isUpdateBirthShow = true"/>
      <!-- 一般情况下，因为要考虑服务器所在的时区/前端客户端所在的时区，所展示的时间保持统一， 后端返回过来的时间参数，一定要求他们为更具时区调整好的字符串 -->
    </div>

    <!-- 编辑昵称 -->
    <van-popup v-model="isUpdateNameShow" position="bottom" :style="{ height: '100%' }" >
      <update-name @close="isUpdateNameShow = false" v-model="user.name"></update-name>
    </van-popup>

    <!-- 编辑性别 -->
    <van-popup v-model="isUpdateGenderShow" position="bottom">
      <update-gender @close="isUpdateGenderShow = false" v-model="user.gender"></update-gender>
    </van-popup>

    <!-- 编辑生日 -->
    <van-popup v-model="isUpdateBirthShow" position="bottom">
      <update-birthday @close="isUpdateBirthShow = false" v-model="user.birthday"></update-birthday>
    </van-popup>

    <!-- 头像上传 -->
    <!-- 上传控件（隐藏） -->
    <input type="file" hidden ref="file" @change="onFileChange"/>

    <!-- 头像编辑弹出层 -->
    <van-popup v-model="isUpdatePhotoShow" position="bottom" :style="{ height: '100%' }">
      <update-photo v-if="isUpdatePhotoShow" @close="isUpdatePhotoShow = false" v-model="image" @update-photo="user.photo = $event"></update-photo>
    </van-popup>
  </div>
</template>

<script>
import UpdateName from './components/update-name.vue'
import UpdateGender from './components/update-gender.vue'
import UpdateBirthday from './components/update-birthday.vue'
import UpdatePhoto from './components/update-photo.vue'
import { getUserProfileAPI } from '../../api/index.js'
export default {
  name: 'UserProfile',
  components: {
    UpdateName,
    UpdateGender,
    UpdateBirthday,
    UpdatePhoto
  },
  data () {
    return {
      user: {}, // 个人信息
      // user.gender 性别 0-男， 1-女
      isUpdateNameShow: false, // 编辑昵称
      isUpdateGenderShow: false, // 编辑性别
      isUpdateBirthShow: false, // 编辑生日
      isUpdatePhotoShow: false, // 编辑头像
      image: null // 临时图片存放点
    }
  },

  created () {
    // 获取用户基本信息
    this.loadProfile()
  },

  methods: {
    // 获取用户基本信息
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
      } catch (error) {
        this.$toast('数据获取失败')
      }
    },

    // 上传图片
    onFileChange () {
      // 获取文件对象
      const file = this.$refs.file.files[0]
      // console.log(this.$refs.file.files[0])

      // 基于文件对象获取 blob数据
      this.image = window.URL.createObjectURL(file)

      // 当使用完成后，情况上传控件的value值，防止后续的操作受到影响
      this.$refs.file.value = ''

      // 展示预览图像编辑弹出层
      this.isUpdatePhotoShow = true
    }
  }
}
</script>

<style lang="less" scoped>
.user-profile {
  .main-wrap {
    padding-top: 92px;
  }

  .avatar {
     width: 52px;
     height: 52px;
  }

  .van-popup {
    background-color: #f2f2f2;
  }
}
</style>
