<template>
  <div>
    <van-picker
      title="编辑性别"
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="$emit('close')"
      :default-index="localGender"
    />
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
/*
  1. 与功能相近的业务组件进行对比，将一些业务上的数据处理共同点直接移植过来
  2. 查阅ui组件库，选择与该业务相贴切的组件，并且阅读属性与事件说明文档，选出该业务相关的属性与方法实施到代码中来
  3. 在页面上实际使用，去修改部分逻辑中的变量或属性来适应当前业务
  4. 查看页面结构是否需要优化，数据流转是否需要修改
*/
export default {
  name: 'UpdateGender',
  props: {
    value: { // 通过v-model获取用户数据中的性别，后续可以使用$emit('input')来进行修改
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      columns: ['男', '女'],
      localGender: this.value
    }
  },
  methods: {
    // 确认修改昵称
    async onConfirm (item, index) {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      /*
        value 是文本
        index 是后端接口需要接收的数字
      */
      try {
        await updateUserProfileAPI({
          gender: index
        })

        // 更新视图内容
        this.$emit('input', index)

        // 关闭弹出层
        this.$emit('close')
        this.$toast.success('保存成功')
      } catch (error) {
        this.$toast.fail('修改失败')
      }
    }
  }
}
</script>

