<template>
  <div class="update-birthday">
    <van-datetime-picker
      v-model="localDate"
      type="date"
      title="选择生日"
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit('close')"
      @confirm="onConfirm"
    />
    <!--
      1. minDate,maxDate,localDate 在时间选择组件中，必须是Date实例化的对象
     -->
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: { // 通过v-model获取用户数据中的生日，后续可以使用$emit('input')来进行修改
      type: String,
      default: ''
    }
  },
  data () {
    return {
      minDate: new Date(1900, 0, 1),
      maxDate: new Date(),
      localDate: new Date(this.value)
    }
  },
  methods: {
    // 确认修改昵称
    async onConfirm (value) {
      // 格式化选中的时间对象
      const tempDate = dayjs(value).format('YYYY-MM-DD')
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      try {
        await updateUserProfileAPI({
          birthday: tempDate
        })

        // 更新昵称视图内容
        this.$emit('input', tempDate)

        // 关闭弹出层
        this.$emit('close')
        this.$toast.success('保存成功')
      } catch (error) {
        this.$toast.fail('修改失败')
      }
    }
  }
}
</script>

