<template>
  <div class="update-photo">
    <img class="img" ref="image" :src="value">
    <div class="toolbar">
      <span class="cancel" @click="$emit('close')">取消</span>
      <span class="confirm" @click="onConfirm">完成</span>
    </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { updateUserPhotoAPI } from '../../../api/index.js'
export default {
  name: 'UpdatePhoto',
  props: {
    // 当父组件传过来的数据类型为object但并不是基本对象类型（{}）, 默认值建议设置为null
    value: { // 头像的blob数据, blob是String
      type: String,
      default: ''
    }
  },

  data () {
    return {
      cropper: null
    }
  },

  mounted () {
    // 初始化剪裁功能CropperJS
    this.initCropper()
  },

  methods: {
    initCropper () {
      // 1. 获取页面图片dom元素
      const IMAGE = this.$refs.image
      // 2. 初始化裁剪
      this.cropper = new Cropper(IMAGE, {
        // 裁剪工具配置项 config
        viewMode: 1, // 定义裁剪模式， 0: 裁剪框可以拓展到画布外， 1,2,3会将裁剪框限制在画布内
        dragMode: 'move', // 拖动模式 move：移动画布
        aspectRatio: 1, // 裁剪比例1:1 可以简写成1
        autoCropArea: 1, // 自动裁图区
        cropBoxMovable: false, // 裁图区域可否移动
        cropBoxResizable: false, // 裁图区域可否缩放
        background: false, // 是否显示默认背景色
        movable: true // 背景图片可否移动，默认就是true
        // crop (event) { // 当移动图片是会触发的方法
        //   const eventObj = {
        //     x: event.detail.x,
        //     y: event.detail.y,
        //     width: event.detail.width,
        //     height: event.detail.height,
        //     rotate: event.detail.rotate,
        //     scaleX: event.detail.scaleX,
        //     scaleY: event.detail.scaleY,
        //     detail: event.detail
        //   }
        //   console.log(eventObj)
        // }
      })
    },

    /*
      问题：
      1. 提交接口时，还可以继续点击提交事件
      2. 当接口更新头像成功后，页面并没有更新最新的头像图片
    */

    onConfirm () {
      // 方案一： 将剪裁坐标通过接口发送给后端
      // 1. 获取坐标数据
      console.log(this.cropper.getData())
      // 2. 将获取的坐标数据发送接口

      // 方案二： 前端使用cropperjs实现图像剪裁，并将剪裁好的图片发送给后端保存
      // 通过 this.cropper.getCroppedCanvas().toBlob() 方法流程，让剪裁区域中的图像进行处理
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        // console.log(blob)
        // 获取到图像blob对象后，进行接口的调用与图片的上传
        this.updateUserPhoto(blob)
      })
    },

    async updateUserPhoto (blob) {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      // 如果接口请求是发送文件，那么需要以FormData对象进行数据传输
      const formData = new FormData()

      // 将blob存到formData中
      // formData.append('key属性名', 'value属性值')
      formData.append('photo', blob)

      try {
        const { data: res } = await updateUserPhotoAPI(formData)

        // 提示成功
        this.$toast.success('更新头像成功')

        // 触发父组件的自定义事件，来修改个人信息对象中的photo属性，实现头像图片的切换
        this.$emit('update-photo', res.data.photo)
        // this.$emit('input')  :image.sync => this.$emit('image')

        // 关闭弹出层
        this.$emit('close')
      } catch (error) {
        // 提示失败
        this.$toast.fail('更新头像失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-photo {
  background-color: #000;
  height: 100%;

  .toolbar {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;

    .cancel, .confirm {
      font-size: 40px;
      margin: 12px;
    }
  }

  .img {
    display: block;
    max-width: 100%;
  }
}
</style>
